import React, {useState} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import {SaveOutlined} from "@ant-design/icons";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Card, Col, DatePicker, Form, Input, message, Row, TimePicker} from "antd";
import moment from "moment";
import PreloaderText from "../../../../../../../components/animated-components/preloader-text";
import ApiService from "../../../../../../../services/api.service";
import {SocketSend} from "../../../../../../../redux/actions/websocket.action";
import AdvertiseForm from "../components/advertise.form";
import CustomTab from "../../../../../../../components/layout-components/custom.tab";
import AdvertiseMarqueForm from '../components/advertise-marque.form'

function mapStateToProps(state) {
	return {};
}

function Index(props) {
	
	
	const [form] = Form.useForm();
	const [loading, setLoading] = useState(false);
	const onFinish = () => {
		setLoading(true)
		message.loading({
			key: "create-display-system",
			content: <PreloaderText text={'Loading'} dots={3} speed={300}/>,
			duration: 5
		})
		form.validateFields()
			.then((value) => {
				if (typeof (value?.data) !== 'undefined' && value?.data && typeof (value?.data) === 'object' && Object.keys(value?.data).length > 0) {
					Object.entries(value?.data).forEach(([key, val]) => {
						if (key === 'start_time' || key === 'end_time') {
							if (moment(val).isValid()) {
								Reflect.set(value.data, key, moment(val).format("HH:mm"))
							}
						}
					})
				}
				setLoading(false)
				
			})
			.catch((err) => {
				message.error({
					key: "create-display-system",
					content: err?.message ?? "Some Error Field",
					duration: 5
				})
			})
	}
	
	return (
		<>
			<HeroComponents
				loading={false}
				name={"Create"}
				positionName={['Advertise']}
				thumbnail={'/create.png'}
				positionColor={'cyan'}
				extra={<>
					<ButtonPermission
						permission={['admin']}
						theme={{
							type: "notification",
							property: {
								message: "Access Denied",
								description: "Anda tidak dapat menyimpan ke database "
							}
						}}
						config={{
							loading: loading,
							icon: <SaveOutlined/>,
							type: "primary",
							danger: false,
							ghost: true,
							size: "small",
							onClick: function () {
								onFinish();
							}
						}}>
						<span>Save</span>
					</ButtonPermission>
				</>}
			/>
			
			<div style={{marginTop:90}}>
				<ContainerTailwind>
					<AdvertiseForm form={form}/>
				</ContainerTailwind>
			</div>
		</>
	);
}

export default connect(
	mapStateToProps, {SocketSend}
)(Index);