import React, {useState, useEffect} from 'react';
import {Badge, Card, Col, Form, Input, message, Row, Select, DatePicker, TimePicker, Upload} from 'antd'
import moment from "moment";
import ThinClientSelectForm from "../../../../../components/forms/thin-client.select.form";
import {findKey} from "../../../../../../../utils";
import {InboxOutlined} from "@ant-design/icons";
const { Dragger } = Upload;
/**
 *
 * @param {object} props
 * @param {'CREATE' | 'UPDATE'} props.mode
 * @param {object | Array | []} props.initial
 * @returns {JSX.Element}
 */
function AdvertiseForm(props) {
    let { mode,initial,form } = props
    const [ initialValues, setInitialValues ] = useState({})
    const [type,setType ] = useState('image')
    useEffect(()=> {
        if(mode === 'UPDATE'){

            let field = {}
            if(typeof(initial) === 'object' && initial && Object.keys(initial).length > 0){
                Object.keys(initial).forEach((key)=> {
                    if(typeof(initial[key]) === "object" && initial[key] && Object.keys(initial[key]).length > 0){
                        Object.keys(initial[key]).forEach((k)=> {
                            if(k === 'start_time' || k === 'end_time'){
                                Reflect.set(initial[key],k,moment(initial[key][k],'HH:mm'))
                            }
                        })
                    }
                    Reflect.set(field,key,initial[key])
                })
            }
            form.setFieldsValue({
                identifier: field?.identifier ?? null,
                data: {
                    ...field?.data
                }
            });

        }
    },[mode,initial,form])
    
    
    function onChangeType(value){
        setType(value)
        form.setFieldsValue({
            type: value
        })
    }
    
    const imageProps = {
        name: 'file',
        multiple: true,
        action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
        onChange(info) {
            const { status } = info.file;
            if (status !== 'uploading') {
                console.log(info.file, info.fileList);
            }
            if (status === 'done') {
                message.success(`${info.file.name} file uploaded successfully.`);
            } else if (status === 'error') {
                message.error(`${info.file.name} file upload failed.`);
            }
        },
    }

    return (
        <React.Fragment>
            <Form
                form={form}
                layout={'vertical'}
                initialValue={{
                    ...initial
                }}
            >
                <Row gutter={24}>
                    <Col xs={24} lg={16}>
                        {
                            type === 'image' ?
                              <div>
                                  <Card borderd={false}>
                                      <Dragger {...imageProps}>
                                          <p className="ant-upload-drag-icon">
                                              <InboxOutlined />
                                          </p>
                                          <p className="ant-upload-text">Click or drag file to this area to upload<br/>1080 x 268px</p>
                                          <p className="ant-upload-hint">
                                              Support for a single or bulk upload. Strictly prohibit from uploading company data or other
                                              band files
                                          </p>
                                      </Dragger>
                                  </Card>
                                  <Card borderd={false}>
                                      <Dragger {...imageProps}>
                                          <p className="ant-upload-drag-icon">
                                              <InboxOutlined />
                                          </p>
                                          <p className="ant-upload-text">Click or drag file to this area to upload<br/>540 x 871px</p>
                                          <p className="ant-upload-hint">
                                              Support for a single or bulk upload. Strictly prohibit from uploading company data or other
                                              band files
                                          </p>
                                      </Dragger>
                                  </Card>
                              </div>
                           :
                              <p>video</p>
                        }
                    </Col>
                    <Col xs={24} lg={8}>
                        <Card bordered={false}>
                            <Form.Item name={'type'} label={'Type Ads'} rules={[{required:true,message:"Can't be empty!"}]} hasFeedback initialValue={type}>
                                <Select defaultValue={type} onChange={onChangeType}>
                                    <Select.Option key={'image'} value={'image'}>image</Select.Option>
                                    <Select.Option key={'video'} value={'video'}>video</Select.Option>
                                </Select>
                            </Form.Item>
                            <ThinClientSelectForm
                              form={{
                                  hasFeedback:true,
                                  rules:[{
                                      required:true,
                                      message: "Can't be empty!"
                                  }]
                              }}
                              select={{
                                  showSearch:true,
                                  onChange: (value,args)=> {
                                      console.log({args,value})
                                      form.setFieldsValue({
                                          thin_client:findKey(args,'id') ?? value
                                      })
                                  },
                                  placeholder:"Search to Select",
                                  
                                  filterOption:(input, option) => (option?.display_name ?? '').includes(input)
                              }}
                              name={'thin_client'}
                              label={'Thin Client'}>
                                {({item,index})=> {
                                  return (
                                    <Select.Option value={findKey(item,['id'])} {...item}>
                                        {[findKey(item,['display_name']),findKey(item,['ip_address'])].join(" | ")}
                                    </Select.Option>
                                  )
                                }}
                            </ThinClientSelectForm>
                            
                            <Form.Item name={'status'} label={'Status'} rules={[{required:true,message:"Can't be empty!"}]} hasFeedback initialValue={'active'}>
                                <Select defaultValue={'active'} showArrow>
                                    <Select.Option value={'active'} key={'active'}><Badge color="cyan" text="Active" /></Select.Option>
                                    <Select.Option value={'inactive'} key={'in-active'}><Badge color="orange" text="In-Active" /></Select.Option>
                                    <Select.Option value={'disabled'} key={'disabled'}><Badge color="gray" text="Disabled" /></Select.Option>
                                </Select>
                            </Form.Item>
                            
                            
                            <Row gutter={24}>
                                <Col lg={12} xs={24}>
                                    <Form.Item name={['data', 'start_date']} label={'Start Date'} hasFeedback
                                               rules={[{required: true, message: "Can't be empty"}]}>
                                        <DatePicker showTime format={'DD/MM/YYYY HH:mm'}/>
                                    </Form.Item>
                                </Col>
                                <Col lg={12} xs={24}>
                                    <Form.Item name={['data', 'end_time']} label={'End Time'} hasFeedback
                                               rules={[{required: true, message: "Can't be empty"}]}>
                                        <DatePicker showTime format={'DD/MM/YYYY HH:mm'}/>
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Card>
                        <Card>
                            <Form.Item name={'remarks'} label={'Remarks'} hasFeedback>
                                <Input.TextArea allowClear showCount/>
                            </Form.Item>
                        </Card>
                    </Col>
                </Row>

            </Form>
        </React.Fragment>
    );
}

export default AdvertiseForm;